<template>
  <div>
    <div id="topBox">
      <el-button @click="dialogVisible = true">分配派送人员</el-button>
    </div>
    <el-tabs type="border-card">
      <el-tab-pane label="分配订单">
        <el-table
          ref="multipleTable"
          :data="tableData"
          tooltip-effect="dark"
          style="width: 100%"
          @selection-change="handleSelectionChange"
          :default-sort="{ prop: 'OrderId', order: 'descending' }"
        >
          <el-table-column type="selection" width="55px"> </el-table-column>
          <el-table-column prop="OrderId" label="订单号" sortable>
          </el-table-column>
          <el-table-column prop="address" label="收货地址"></el-table-column>
          <el-table-column
            prop="ExpressType"
            label="快递公司"
          ></el-table-column>
          <el-table-column prop="PickCode" label="收货码"> </el-table-column>
          <el-table-column prop="isroom" label="是否送寝"> </el-table-column>
          <el-table-column prop="price" label="配送价格"> </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="已分配订单">
        <el-table
          ref="multipleTable"
          :data="FinishedOrderList"
          tooltip-effect="dark"
          style="width: 100%"
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" width="55px"> </el-table-column>
          <el-table-column prop="OrderId" label="订单号" sortable>
          </el-table-column>
          <el-table-column prop="address" label="收货地址"></el-table-column>
          <el-table-column
            prop="ExpressType"
            label="快递公司"
          ></el-table-column>
          <el-table-column prop="PickCode" label="收货码"> </el-table-column>
          <el-table-column prop="isroom" label="是否送寝"> </el-table-column>
          <el-table-column prop="price" label="配送价格"> </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>

    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose"
    >
      <div>
        <el-select v-model="value" placeholder="请选择">
          <el-option
            v-for="item in Account"
            :key="item.Account"
            :value="item.Account"
          >
          </el-option>
        </el-select>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="send()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      dialogVisible: false,
      multipleSelection: [],
      options: [], // 取件号码
      value: "",
      OrderList: [],
      FinishedOrderList: [],
      Account: [],
    };
  },
  created() {
    this.getdata();
    this.getnotpick();
    this.getPickAccount();
  },
  methods: {
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then((_) => {
          done();
        })
        .catch((_) => {});
    },
    toggleSelection(rows) {
      console.log(rows);
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
      let list = [];
      val.map((item) => {
        list.push(item.OrderId);
      });
      this.OrderList = list;
    },

    // 请求数据
    async getdata() {
      // console.log(localStorage.token);
      console.log(this.$MainGet);
      let res = await this.$MainGet.get(this.$beseURl + "/getData/getAllOrder");
    },
    async getnotpick() {
      let res = await this.$MainGet.get(
        this.$beseURl + "/getData/getNotAllocationPickOrder"
      );
      console.log(456, res);
      let list1 = [];
      let list2 = [];
      this.tableData = res.data;
      res.data.map((item) => {
        if (item.PickPersonPhone != null) {
          list1.push(item);
        } else {
          list2.push(item);
        }
        this.FinishedOrderList = list1;
        this.tableData = list2;
      });
    },
    async getPickAccount() {
      let res = await this.$MainGet.get(
        this.$beseURl + "/getData/getPickAccount"
      );
      this.Account = res.data;
      console.log(this.Account);
    },
    async send() {
      console.log(this.value);
      for (let i = 0; i < this.OrderList.length; i++) {
        let data = {
          OrderId: this.OrderList[i],
          PickPersonPhone: this.value,
        };
        let res = await this.$MainGet.post(
          this.$beseURl + "/SentData/AllocationPickPersonOrder",
          data
        );
        console.log("返回值为：", res);
        if (res.code == "0") {
          this.$message({
            message: "提交成功",
            type: "success",
          });
        } else {
          this.$message({
            showClose: true,
            message: "提交失败",
            type: "error",
          });
        }
      }
      this.dialogVisible = false;
    },
  },
};
</script>

<style scoped>
#middleBox {
  width: 400px;
  height: 200px;
  background-color: rgb(255, 255, 255);
  border-radius: 2%;
  position: absolute;
  left: 50%;
  top: 40%;
  transform: translate(-50%, -50%);
  z-index: 20;
}
#middleBox .el-select {
  margin-top: 20px;
}
#topBox {
  margin-bottom: 10px;
}
</style>