import Vue from 'vue'
import VueRouter from 'vue-router'
import historyForm from '../views/historyForm.vue'
import Login from '../views/Login.vue'
import orderForm from '../views/orderForm.vue'
Vue.use(VueRouter)

const routes = [{
    path: '/',
    redirect: '/Login'
  },
  {
    path: '/Login',
    component: Login
  },
  {
    path: '/About',
    component: () => import('../views/About.vue'),
    children: [{
        path: '/historyForm',
        component: historyForm,
      },
      {
        path: '/orderForm',
        component: orderForm
      },
      {
        path: '/mangageMent',
        component: () => import('../views/mangageMent.vue')
      },
      {
        path: '/globalManage',
        component: () => import('../views/globalManage.vue')
      },

      {
        path: '/DeliveryOrder',
        component: () => import('../views/DeliveryOrder.vue')
      }
    ]
  },
]

const router = new VueRouter({
  mode: 'history',   // 模式，默认hash
  base: '/dist', // 基础路径

  
  routes
})

export default router