<template>
  <div class="Login">
    <div class="demo-input-suffix">
      账号：<input v-model="user.account" id="zhanghao" /><br />

      密码：<input
        type="password"
        addon-before="密码"
        class="inputbox"
        v-model="user.passwd"
      /><br />

      <div class="button-area">
        <button type="primary" @click="login" style="margin-left: 50px">
          登录
        </button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      user: {
        account: "",
        passwd: "",
      },
    };
  },
  methods: {
    async login() {
      let data = {
        Account: "18582707259",
        password: "987654",
      };
      try {
        // this.user.passwd =
        let res = await this.$MainGet.post(`${this.$beseURl}/user/login`, data);
        console.log(123, res);

        localStorage.token = res.token;
        if (res.token != "") {
          this.$message.success("登录成功");
          if (localStorage.token) {
            console.log(localStorage.token);
            this.$router.push("/About");
          }
        } else {
          throw "登录失败";
        }
      } catch (err) {
        this.$message.warning("账户或密码错误");
      }
      this.$competence = 0;
    },
  },
};
</script>

<style scope>
body,
html {
  margin: 0;
  padding: 0;
}
.Login {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(235, 235, 235);
}
#zhanghao {
  width: 300px;
  height: 30px;
}
.input-area {
  width: 500px;
  height: 400px;
  border: 1px solid red;
  box-shadow: 0 100px 80px rgba(0, 0, 0, 0.09);
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.inputbox {
  width: 300px;
  height: 30px;
  margin: 20px 0;
  background-color: rgb(235, 235, 235);
}
.button {
  margin: 20px 20px;
}
</style>
